<template>
  <div class="d1 flex">
    <div class="left col-sm-12">
      <criteria-paged :model="model" v-ref:cp>
        <criteria partial='criteria' @condition-changed='search' v-ref:cri>
          <form novalidate class="" partial>
            <div class="col-sm-2">
              <div class="col-sm-6">
                <input type="hidden" class="form-control" v-model="model.id"
                       v-on:keyup.enter="search"
                       condition="id like '{}%'">
              </div>
            </div>
            <div class="col-sm-1">
              <button type="hidden" @click="search(),$parent.$parent.close()">
              </button>
            </div>
          </form>
        </criteria>
        <data-grid :model="model" partial='list' v-ref:grid class="data-grid" is-fixed='false' style="overflow-x:auto">
          <template partial='head'>
            <tr class="title">
              <th>
                <nobr>序号</nobr>
              </th>
              <th>
                <nobr>状态</nobr>
              </th>
              <th>
                <nobr>录单日期</nobr>
              </th>
              <th>
                <nobr>单据编号</nobr>
              </th>
              <th>
                <nobr>联系电话</nobr>
              </th>
              <th>
                <nobr>地址</nobr>
              </th>
              <th>
                <nobr>合计(元)</nobr>
              </th>
              <th>
                <nobr>制单人</nobr>
              </th>
              <th>
                <nobr>操作</nobr>
              </th>
            </tr>
          </template>
          <template partial='body'>
            <tr>
              <td>
                <nobr>{{$index+1}}</nobr>
              </td>
              <td>
                <nobr>{{row.f_status}}</nobr>
              </td>
              <td>
                <nobr>{{row.f_date}}</nobr>
              </td>
              <td>
                <nobr>{{row.f_billnumber}}</nobr>
              </td>
              <td>
                <nobr>{{row.f_phone}}</nobr>
              </td>
              <td>
                <nobr>{{row.f_address}}</nobr>
              </td>
              <td>
                <nobr>{{row.f_sum}}</nobr>
              </td>
              <td>
                <nobr>{{row.f_username}}</nobr>
              </td>
              <td>
                <a href="#" @click="$parent.$parent.$parent.details(row)">查看详情</a>
              </td>
            </tr>
          </template>
        </data-grid>
      </criteria-paged>
    </div>
    <modal :show.sync="show" v-ref:modal backdrop="false">
      <header slot="modal-header" class="modal-header">
        <button type="button" class="close" @click="close"><span>&times;</span></button>
      </header>
      <article slot="modal-body" class="modal-body">
        <div style="width:auto;height: auto">
          <criteria-paged v-ref:page1 :model="model1" :pager='false'>
            <criteria v-ref:xiaoshoudan partial='criteria' @condition-changed='search'>
              <div novalidate class="form-inline auto" partial>
                <div class="form-group">
                  <!--<span class="btn btn-default" @click="search()"></span>-->
                  <!--<report-print id='shexianss' top='1cm' left='1cm' width='100%' height='100%'></report-print>-->
                </div>
              </div>
            </criteria>
            <div partial='list' id='shexianss1'>
              <table class='tablesty' style="margin: 0px auto;" border="1">
                <tr>
                  <th colspan='4' style="font-weight: normal;">
                    {{{ model.data.substring(26,model.data.length-8) }}}
                    <!--{{model.data}}-->
                  </th>
                </tr>
              </table>
              <div class="col-sm-12" style="margin-top: 2px">
                <div class="col-sm-2">
                  <label>
                    <nobr> 补打原因：</nobr>
                  </label>
                </div>
                <div class="col-sm-6">
                  <input style="width: 100%" v-model="model.f_again_reason" :value.sync="model.f_again_reason"/>
                </div>
                <div class="col-sm-2">
                  <report-print v-if="model.f_again_reason" @click="$parent.$parent.updateNumber()" id='shexianss1' top='1cm' left='1cm'
                                width='100%' height='100%'></report-print>
                </div>
              </div>
            </div>

          </criteria-paged>
        </div>
      </article>
      <footer slot="modal-footer" class="modal-footer">
        <!--<button v-show="show" type="button" class="btn btn-default" @click='close'>取消</button>-->
        <!--<button v-show="show" type="button" class="btn btn-success" @click='confirm'>确认</button>-->
      </footer>
    </modal>
  </div>
</template>

<script>
  import {DataModel} from 'vue-client'
  import  {HttpResetClass} from 'vue-client'
  import {
    PagedList
  } from 'vue-client'

  export default {
    title: "销售记录单表历史记录",
    props: ['billselect', 'selectone'],
    data() {
      return {
        show: false,      // 详情显示控制(模态框)
        model: new PagedList('rs/sql/billrecord', 20, {
          data: {
            f_process_id: this.billselect.f_process_id,
            fengongsi: this.$login.f.f_fengongsi,
            f_bill_type: this.selectone
          }
        }),
        model1: new DataModel('rs/report/xiaoshoudanlishi', {
          data: {
            selectid: this.selectid
          }
        }),
        selectid: null,
        selectdata:null
      }
    },
    ready() {
      this.$refs.cp.$refs.cri.search()
    },
    methods: {
      details(row) {
        console.log('选中的id:'+row)
        this.selectid = row.id
        this.selectdata = row
        this.show = true
        this.model1.paramSource.data.selectid = this.selectid
        this.$refs.page1.$refs.xiaoshoudan.search()
      },
      close(){
        this.show=false
      },
      async updateNumber(){
        let http = new HttpResetClass()
        let billinfo = this.selectdata
        delete billinfo.id
        // 票据类型
        billinfo.f_bill_type = this.selectone
        billinfo.f_status = '补打'
        let data = {
          billinfo: billinfo
        }
        await http.load('POST', 'rs/logic/makebillrecord', {data: data}, {resolveMsg: null, rejectMsg: "开通卡记录存储失败"})
      }
    }
  }
</script>

<style scoped>

</style>



